// src/context/Web3Context.tsx
'use client';
import {
    createContext,
    useContext,
    useState,
    useEffect,
    ReactNode,
  } from 'react';
  import Web3 from 'web3';
  import { AbiItem } from 'web3-utils';
  import { CONTRACT_ABI, CONTRACT_ADDRESS } from '@/lib/constants';
  
  interface Web3ContextType {
    account: string | null;
    connectWallet: () => Promise<void>;
    disconnectWallet: () => void;
    publishPost: (message: string) => Promise<void>;
    likePost: (index: number) => Promise<void>;
    dislikePost: (index: number) => Promise<void>;
    removeLike: (index: number) => Promise<void>;
    removeDislike: (index: number) => Promise<void>;
    modifyPost: (index: number, message: string) => Promise<void>;
    getPosts: () => Promise<void>;
    posts: any[];
    loading: boolean;
  }
  
  const Web3Context = createContext<Web3ContextType | undefined>(undefined);
  
  export function Web3Provider({ children }: { children: ReactNode }) {
    const [web3, setWeb3] = useState<Web3 | null>(null);
    const [account, setAccount] = useState<string | null>(null);
    const [contract, setContract] = useState<any>(null);
    const [posts, setPosts] = useState<any[]>([]);
    const [loading, setLoading] = useState(false);
  
    const connectWallet = async () => {
      if (typeof window.ethereum !== 'undefined') {
        try {
          const accounts = await window.ethereum.request({
            method: 'eth_requestAccounts',
          });
          const web3Instance = new Web3(window.ethereum);
          const contractInstance = new web3Instance.eth.Contract(
            CONTRACT_ABI as AbiItem[],
            CONTRACT_ADDRESS
          );
  
          setWeb3(web3Instance);
          setAccount(accounts[0]);
          setContract(contractInstance);
        } catch (error) {
          console.error('Error connecting to wallet:', error);
        }
      } else {
        alert('Please install MetaMask!');
      }
    };
  
    const disconnectWallet = () => {
      setWeb3(null);
      setAccount(null);
      setContract(null);
    };
  
    const getPosts = async () => {
      if (!contract) return;
  
      try {
        setLoading(true);
        const totalPosts = await contract.methods.getTotalPosts().call();
        const postsData = await Promise.all(
          Array.from({ length: Number(totalPosts) }, (_, i) =>
            contract.methods.getPost(i).call()
          )
        );
        setPosts(postsData);
      } catch (error) {
        console.error('Error fetching posts:', error);
      } finally {
        setLoading(false);
      }
    };
  
    const publishPost = async (message: string) => {
      if (!contract || !account) return;
  
      try {
        await contract.methods.publishPost(message).send({ from: account });
        await getPosts();
      } catch (error) {
        console.error('Error publishing post:', error);
      }
    };
  
    // Implement other contract interactions similarly...
  
    useEffect(() => {
      if (contract) {
        getPosts();
      }
    }, [contract]);
  
    return (
      <Web3Context.Provider
        value={{
          account,
          connectWallet,
          disconnectWallet,
          publishPost,
          likePost,
          dislikePost,
          removeLike,
          removeDislike,
          modifyPost,
          getPosts,
          posts,
          loading,
        }}
      >
        {children}
      </Web3Context.Provider>
    );
  }
  
  export function useWeb3Context() {
    const context = useContext(Web3Context);
    if (context === undefined) {
      throw new Error('useWeb3Context must be used within a Web3Provider');
    }
    return context;
  }